import React from 'react'
import 'bootstrap/dist/css/bootstrap.css';
import '../css/siteStyle.css';
import { Navbar, Container, Nav, NavDropdown } from 'react-bootstrap';
import Home from '../Pages/home';
function Navigation() {
    return (
        <Navbar expand="lg" id='navbar' >
            <Container>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link href="/">Home</Nav.Link>
                        <NavDropdown title="Product" id="navbarScrollingDropdown">
                            <NavDropdown.Item href="/beachwear">Beachwear</NavDropdown.Item>
                            <NavDropdown.Item href="/business">Business</NavDropdown.Item>
                            <NavDropdown.Item href="/casual">Casual</NavDropdown.Item>
                            <NavDropdown.Item href="/sports">Sports</NavDropdown.Item>
                        </NavDropdown>
                      
                        <Nav.Link href="/ShoppingCart">ShoppingCart</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>

    )
}

export default Navigation;
