import React from 'react'
import 'bootstrap/dist/css/bootstrap.css';
import { Carousel } from 'react-bootstrap';
import img6 from '../assets/Images/Image6.jpg';
import img7 from '../assets/Images/Image7.jpg';
import img8 from '../assets/Images/Image8.jpg';
import img9 from '../assets/Images/Image9.jpg';

function CarouselInfo() {
   return (
       <>
       <br></br>
           <div id='headerSection'>Hot Products</div>
           <br></br>
       <Carousel fade>
           <Carousel.Item>
               <img src={img6} alt="image1" id='picInfo'/>
               <Carousel.Caption>
                   <a id='linkStyle' href='/business' >Pink Suite</a>
               </Carousel.Caption>
           </Carousel.Item>
           <Carousel.Item>
               <img src={img7} alt="image1" id='picInfo'/>
               <Carousel.Caption>
               <a id='linkStyle' href='/sports' >Men Sport Set</a>
               </Carousel.Caption>
           </Carousel.Item>
           <Carousel.Item>
               <img src={img8} alt="image1" id='picInfo'  />
               <Carousel.Caption>
               <a id='linkStyle' href='/casual' >white Sweatshirt</a>
               </Carousel.Caption>
           </Carousel.Item>
           <Carousel.Item>
               <img src={img9} alt="image1" id='picInfo'/>
               <Carousel.Caption>
               <a id='linkStyle' href='/beachwear' >Men Floral Shirt</a>
               </Carousel.Caption>
           </Carousel.Item> 
       </Carousel>
       </>
   );
}

export default CarouselInfo;
