import React from "react";
import { useState, useEffect } from "react";
//import './App.css';
import 'bootstrap/dist/css/bootstrap.css';

function Billing() {
  return (
    <div className="container">
      <div className="container">
        <div id='title'>Shine</div>
        <div className="py-5 text-center">
          <h1>Checkout Form</h1>
        </div>
      </div>
      <h4 className="mb-3">Shipping method</h4>
      <hr class="my-4" />
      <div class="col-12">
        <div className="form-check">
          <input id="sameaddress" type="checkbox" class="form-check-input" />
          <label className="form-check-label" for="sameAddress">Standard shipping</label>
        </div>
        <div className="form-check">
          <input id="saveInfo" type="checkbox" class="form-check-input" />
          <label className="form-check-label" for="saveInfo">Express shipping</label>
        </div>
      </div>
      <hr class="my-4" />
      <h4 className="mb-3">Billing Address</h4>
      <form>
        <div class="row g-3">
          <div class="col-sm-6">
            <label for="FirstName" class="form-label">First Name</label>
            <input id="FirstName" type="text" class="form-control"
              placeholder="John" required />
          </div>
        </div>
        <div class="col-sm-6">
          <label for="LastName" class="form-label">Last Name</label>
          <input id="lastName" type="text" class="form-control"
            placeholder="Smith" required />
          <div class="invalid-feedback">
            Valid last name is required.
          </div>
        </div>

        <div class="col-12">
          <label for="username" class="form-label">Street Address</label>
          <div class="input-group">
            <input id="username" type="text" class="form-control"
              placeholder="213 Fox Street" required />
          </div>
        </div>


        <div class="col-md-5">
          <label for="country" class="form-label">Country</label>
          <select id="country" class="form-control">
            <option value="">Choose..</option>
            <option value="Australia">Australia</option>
            <option value="USA">USA</option>
            <option value="Canada">Canada</option>
          </select>
        </div>

        <div class="col-md-4">
          <label for="state" class="form-label">State</label>
          <select id="state" class="form-control">
            <option value="">Choose..</option>
            <option value="USA">PA</option>
            <option value="USA">NY</option>
            <option value="USA">TX</option>
          </select>
        </div>


        <div class="col-md-3">
          <label for="zipcode" class="form-label">Zipcode</label>
          <input id="zipcode" type="text" class="form-control"
            placeholder="12345" required />
        </div>

        <hr class="my-4" />
        <div class="col-12">
          <div className="form-check">
            <input id="sameaddress" type="checkbox" class="form-check-input" />
            <label className="form-check-label" for="sameAddress">Shipping address is the same as
              billing</label>
          </div>
          <div className="form-check">
            <input id="saveInfo" type="checkbox" class="form-check-input" />
            <label className="form-check-label" for="saveInfo">Save this info</label>
          </div>
        </div>

        <hr className="my-4" />
        <h4 className="mb-3">Payments</h4>

        <div className="form-check">
          <input id="creditCard" name="paymentMethod"
            type="radio" class="form-check-input" checked required />
          <label for="creditCard">Credit Card</label>
        </div>

        <div className="form-check">
          <input id="debit" name="paymentMethod"
            type="radio" className="form-check-input" checked required />
          <label for="directDebit">Direct Debit</label>
        </div>

        <div className="form-check">
          <input id="paypal" name="paymentMethod" type="radio"
            className="form-check-input" checked required />
          <label for="directDebit">Paypal</label>
        </div>

        <div className="row my-3 gy-3">
          <div className="col-md-6">
            <label for="fullName" className="form-label">
              Name on card
            </label>
            <input type="text" class="form-control" />
            <small class="text-muted">
              Full name as displayed on card
            </small>
          </div>
          <div class="col-md-6">
            <label for="cc-number" class="form-label">
              Credit Card Number
            </label>
            <input id="cc-number" type="text" class="form-control" />
            <div class="invalid-feedback">
              Credit card number is required
            </div>
          </div>
          <div class="col-md-3">
            <label for="cc-expire" class="form-label">
              Expiration
            </label>

            <input id="cc-expire" type="text" class="form-control" />
            <div class="invalid-feedback">
              Expiration is required
            </div>
          </div>
          <div class="col-md-3">
            <label for="cc-cvv" class="form-label">
              CVV
            </label>

            <input id="cc-cvv" type="text" class="form-control" />
            <div class="invalid-feedback">
              CVV is required
            </div>
          </div>
        </div>

        <hr class="my-4" />
        <h4 className="mb-3">Total Cost {(window.sessionStorage.getItem("Total"))}</h4>

        <hr class="my-4" />
        <div class="container">
          <div class="center">
            <div class="row">
              <div class="col text-center">
                <button class="btn btn-primary btn-lg btn-blick">Checkout</button>
              </div>
            </div>
          </div>
        </div>
      </form >
    </div >
  );
}

export default Billing;