import React from 'react'
import 'bootstrap/dist/css/bootstrap.css';
import { Card } from 'react-bootstrap';
import img2 from '../Images/Image2.jpg';
import img3 from '../Images/Image3.jpg';
import img4 from '../Images/Image4.jpg';
import img5 from '../Images/Image5.jpg';

function Category() {
    return (
        
        <>
            <br></br>
            <div id='headerSection'>Shop By Category</div>
            <br></br>

            <div class="row" >
                <div class="col">
            <Card >               
                <Card.Link href="#link">Casual</Card.Link>               
                    <Card.Img src={img2}  />           
            </Card>
            </div>
            <div class="col">
            <Card >
                <Card.Link href="#link">Sports</Card.Link>

                    <Card.Img src={img3} />
            </Card>
            </div>
            <div class="col">
            <Card  >
                <Card.Link href="#link">Business</Card.Link>
                    <Card.Img src={img4} />
            </Card>
            </div>
            <div class="col">
            <Card  >
                <Card.Link href="#link">Beachwear</Card.Link>
                   <Card.Img src={img5} />
            </Card>
            </div>
            </div>
        </>
    )
}

export default Category;
